/************************************** SETUP SNIPPET **************************************/

body, h1, h2, p {
    margin: 0;
    padding: 0;
}

html {
    font-size: 62.5%;
}

body {
    background-color: #282c34;
    font-size: 2rem;
}

.wrapper {
    max-width: 1400px;
    width: 90%;
    margin: 0 auto;
}

header {
    background-color: gold;
}

h1 {
    text-align: center;
    text-transform: uppercase;
    color: crimson;
    padding: 15px 0;
}

h2 {
    background-color: firebrick;
    text-transform: uppercase;
    color: gold;
    padding: 10px 0;
}

section {
    padding: 25px 0 35px;
}

.cases {
    background-image: url(./marjanblan-unsplash.jpg);
    background-size: contain;
    border: 4px solid firebrick;
    text-align: center;
    width: 80%;
    margin: 0 auto;
}

.cases p {
    font-weight: bold;
    width: 25%;
    margin: 20px auto;
    padding: 5px 0;
}

.cases span {
    display: block;
}

.cases h2 span {
    font-size: 1.6rem;
}

.confirm {
    background-color: darkorange;
}

.death {
    background-color: red;
}

.recover {
    background-color: green;
}

form {
    display: flex;
    justify-content: center;
}

label {
    background-color: firebrick;
    color: gold;
    font-weight: bold;
    text-transform: uppercase;
    padding: 5px 10px;
}

select {
    background-color: gold;
    border: none;
    color: crimson;
    font-weight: bold;
}

footer {
    background-color: gold;
    position: absolute;
    bottom: 0;
    width: 100%;
    padding: 10px 0;
}

footer p {
    text-align: center;
    color: crimson;
    font-weight: bold;
}

a {
    color: crimson;
    text-transform: uppercase;
}

@media (max-width: 1050px) {
    .cases p {
        width: 30%;
    }
}

@media (max-width: 780px) {
    .cases p {
        width: 35%;
    }
    form {
        display: flex;
        flex-direction: column;
    }
    label, select {
        text-align: center;
        width: 80%;
        margin: 0 auto;
    }
    footer {
        position: static;
        margin-top: 35px;
    }
}

@media (max-width: 650px) {
    .cases p {
        width: 45%;
    }
}

@media (max-width: 550px) {
    .cases p {
        width: 60%;
    }
}

@media (max-width: 440px) {
    .cases p {
        width: 90%;
    }
}